.navbar {
    background: linear-gradient(to right, #b23f68, #f8aec7);
    position: sticky;
    top: 0;
    padding: 15px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
    z-index: 1000;
}

.nav-logo {
    font-size: 24px;
    font-weight: bold;
    color: #ffffff;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 29px;
    font-size: 23px;
    box-sizing: border-box;
    color: rgb(255, 255, 255);
}

.nav-links a {
    text-decoration: none;
    color: #ffffff;
    font-family: emoji;
}

.nav-links a:hover {
    background-color: #1e1e1e;
    padding: 15px 25px;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 500;
    color: #7b2929;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
}


